        :visible="inner"
        class="inner_dialog"
        append-to-body
        @close="$emit('update:inner', false)"
      >
        <div class="inner_text_img">
          <div class="inner_text">
            <span>该区域属于{{ business }}商圈适合销售一下商品：</span>
          </div>
          <div class="inner_img">
            <el-row :gutter="20">
              <el-col
                :span="5"
                class="card-panel-col"
