              >
                <div class="inner_img_img">
                  <img :src="item.image" :alt="item.skuName" />
                  <br />
                  <span class="inner_img_text">{{ item.skuName }}</span>
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
        <div slot="footer">
          <el-button class="button_confirm" @click="Confirmadvice"
            >采纳建议</el-button
          >
        </div>
      </el-dialog>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" class="button_confirm" @click="putVmFn"
          >确认</el-button
        >
      </div>
    </el-dialog>
    <!-- 第三个弹窗 -->
    <el-dialog
      title="选择商品"
      width="860px"
      :visible="dialog_three"
      class="dialog_three"
      :close-on-click-modal="false"
      @close="threeClose"
    >
      <div class="three" v-loading="threeLoad">
        <div class="three_top">
          <el-form
            :inline="true"
            :model="form"
            class="demo-form-inline"
            ref="search"
          >
            <el-form-item class="top_input_text" label="商品名称:" prop="id">
              <el-input
                v-model.trim="form.id"
                placeholder="请输入"
                class="top_input"
                size="medium"
                clearable
                @input="getAddGoodsList"
              >
              </el-input>
            </el-form-item>
            <el-button
              class="top_button"
              type="primary"
              icon="el-icon-search"
              size="medium"
              @click.native="getDateS(form.id)"
              >查询</el-button
            >
          </el-form>
        </div>

        <div class="three_botom" v-if="AddGoodsList.currentPageRecords">
          <el-row :gutter="20">
            <el-col
              @click.native="threeClick(item)"
              :span="5"
              class="card-panel-col"
              v-for="item in AddGoodsList.currentPageRecords"
              :key="item.skuI"
              style="cursor: pointer"
            >
              <div class="inner_img_img">
                <template v-if="item.discount">
                  <img :src="isChecked" class="img_cheked" alt="" />
                </template>
                <img :src="item.skuImage" />
                <br />
                <span class="inner_img_text">{{ item.skuName }}</span>
              </div>
            </el-col>
          </el-row>
          <span
            class="three_left_arrow"
            :style="
              AddGoodsList.pageIndex === '1'
                ? 'color:#D9DDE2;cursor:not-allowed;pointer-events:none'
                : 'color:#000000; '
            "
            @click="addPageIndex(-1)"
          >
            <el-icon class="el-icon-arrow-left"></el-icon>
          </span>
          <span
            class="three_right_arrow"
            :style="
              AddGoodsList.pageIndex === AddGoodsList.totalPage
                ? 'color:#D9DDE2;cursor:not-allowed;pointer-events:none'
                : 'color:#000000;'
            "
            @click="addPageIndex(1)"
          >
            <el-icon class="el-icon-arrow-right"></el-icon>
          </span>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" class="button_confirm" @click="threeConfirm"
          >确认</el-button
        >
      </div>
    </el-dialog>
    <!-- 策略弹出层 -->
    <el-dialog
      title="策略管理"
      :visible.sync="adviceDia"
      width="630px"
      @close="adviceDia = false"
    >
      <div class="NotPolicy" v-show="!isPolicy">
        <el-form ref="doMore">
          <!-- 选项一 -->
          <el-form-item label="策略管理:">
            <el-select v-model="policyValue.value" placeholder="请选择">
              <el-option
                v-for="(item, index) in Policy"
                :key="index"
                :label="item.policyName"
                :value="item.policyId"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div class="NotPolicy" v-show="isPolicy">
        <el-row :gutter="20" style="margin-bottom: 20px">
          <el-col :span="12"
            >机器编号:{{ isPolicy ? isPolicy.vmId : "" }}</el-col
          >
          <el-col :span="12"
            >策略编号:{{ isPolicy ? isPolicy.policyId : "" }}</el-col
          >
        </el-row>
        <el-row :gutter="20" style="margin-bottom: 20px">
          <el-col :span="12"
            >机器方案:{{
              isPolicy
                ? isPolicy.discount > 10
                  ? isPolicy.discount + "%"
                  : isPolicy.discount * 10 + "%"
                : ""
            }}</el-col
          >
        </el-row>
      </div>
      <div slot="footer" class="dialog-footer">
        <div v-show="!isPolicy">
          <el-button class="left" size="medium" @click="policyCancel"
            >取消</el-button
          >
          <el-button
            type="primary"
            size="medium"
            class="right"
            @click="addPolicy"
            >确定</el-button
          >
        </div>
        <div v-show="isPolicy">
          <el-button class="left" size="medium" @click="delpolicy"
            >取消</el-button
          >
        </div>
      </div>
    </el-dialog>
    <!-- 修改弹出层 -->
    <el-dialog
      title="修改设备"
      :visible="changeDia"
      width="630px"
      @close="changeDia = false"
    >
      <div class="changeItem" v-if="changeItem.clientId">
        <div class="div_margin">
          机器编号:<span>{{
            changeItem.innerCode ? changeItem.innerCode : ""
          }}</span>
        </div>
        <div class="div_margin">
          供货时间:<span>{{
            changeItem.lastSupplyTime
              ? changeItem.lastSupplyTime.replace(/-/g, ".").replace("T", " ")
              : ""
          }}</span>
        </div>
        <div class="div_margin">
          设备类型:<span>{{
            changeItem.type.name ? changeItem.type.name : ""
          }}</span>
