                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="div_margin">
          &nbsp;&nbsp;&nbsp;合作商:<span>{{
            changeItem.node.name ? changeItem.node.ownerName : ""
          }}</span>
        </div>
        <div class="div_margin">
          所属区域:<span>{{ changeItem.region.name }}</span>
        </div>
        <div class="div_margin">
          设备地址:<span>{{ changeItem.node.name }}</span>
        </div>
      </div>
      <div slot="footer">
        <el-button class="left" size="medium" @click="changeCancel"
          >取消</el-button
        >
        <el-button
          type="primary"
          size="medium"
          class="right"
          @click="changeVmNode"
          >确定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  getAisle,
  getAisleRow,
  getSort,
  getBusiness,
  getAddGoodsList,
  putVm,
  getVmPolicy,
  getPolicy,
  addPolicy,
  delVmPolicy,
  getVmNode,
  changeVmNode
} from '@/api/vm'
export default {
  props: {
    out: {
      type: Boolean,
      default: false
    },
    inner: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      Aisle: [],
      isChecked:
        'data:image/png;base64,',
      img: 'data:image/png;base64,',
      volume: {},
      valueLoading: false,
      adviceList: [],
      business: '',
      dialog_three: false,
      form: {
        id: ''
      },
      AddGoodsList: {},
      indexPage: {},
      threeLoad: false,
      checkItemThird: {},
      checkIndex: {},
      // 策略弹出层
      adviceDia: false,
      chooseItem: {},
      Policy: [],
      policyValue: {
        value: ''
      },
      isPolicy: null,
      // 修改弹出层
      changeDia: false,
      changeItem: {},
      changeOption: [],
      changeSelect: {
        value: ''
      }
    }
  },
  methods: {
    leftFn () {
      this.$refs.img_switch.style.transform = 'translateX(0px)'
      this.$refs.left_arrow.style.color = '#D9DDE2'
      this.$refs.right_arrow.style.color = '#000'
    },
    rightFn () {
      this.$refs.img_switch.style.transform = 'translateX(-830px)'
      this.$refs.right_arrow.style.color = '#D9DDE2'
      this.$refs.left_arrow.style.color = '#000'
    },
    async getAisle (id) {
      const res = await getAisle(id) // 获得饮料数据
      this.Aisle = res
    },
    async getAisleRow (id) {
      const res = await getAisleRow(id) // 获得行数据
      this.volume = res
    },
    async sendItem (data) {
      // 从父组件传来的数据
      this.chooseItem = data
      this.valueLoading = true
      try {
        await this.getAisleRow(data.vmType) // 调用饮料数据
        await this.getAisle(data.innerCode) // 调用行数据
        await this.getAllSort(data.businessId) // 调用建议数据
      } catch (e) {
        console.log(e)
      } finally {
        this.valueLoading = false
      }
    },
    closeFn () {
      // 关闭窗口置空数据
      this.$emit('update:out', false)
      this.Aisle = []
      this.volume = {}
      this.leftFn()
    },
    async getAllSort (id) {
      // 获得地区和建议数据
      const business = await getBusiness(id)
      this.business = business
      const img = await getSort(id)
      this.adviceList = img
    },
    Confirmadvice () {
      // 将推荐商品放入列表
      // eslint-disable-next-line no-unused-vars
      let count = 0
      this.Aisle.forEach((item) => {
        if (!(count === this.adviceList.length)) {
          if (item.sku) {
            if (item.sku.skuId === this.adviceList[count].skuId) {
              count++
            }
          } else {
            item.sku = this.adviceList[count]
            count++
          }
        }
      })
      this.$emit('update:inner', false)
    },
    async putVmFn () {
      // 发布新的总列表
      await putVm(this.putVmFnForData())
      this.$emit('update:out', false)
      this.$parent.getDateS()
    },
    putVmFnForData () {
      // 发布需要的数据
      const data = {
        channelList: this.Aisle.map((item) => {
          return {
            channelCode: item.channelCode,
            skuId: item.sku ? item.sku.skuId : '0'
          }
        }),
        innerCode: this.Aisle[0].innerCode
      }
      return data
    },
    async getAddGoodsList (e) {
      // 第三个框打开
      // 搜索请求
      this.dialog_three = true
      this.threeLoad = true
      let res = {}
      if (e === '') {
        this.indexPage.pageIndex = '1'
        this.indexPage.pageSize = '10'
      }
      if (e) {
        const data = {
          pageIndex: 1,
          pageSize: 10,
          skuName: this.form.id
        }
        res = await getAddGoodsList(data)
      } else {
        res = await getAddGoodsList(this.indexPage)
      }
      // console.log(res)
      const { pageIndex, pageSize, totalCount, totalPage } = res
      this.indexPage = { pageIndex, pageSize, totalCount, totalPage }
      this.AddGoodsList = res
      this.threeLoad = false
    },
    async addPageIndex (e) {
      // 按钮请求
      this.dialog_three = true
      if (
        this.AddGoodsList.pageIndex === this.AddGoodsList.totalPage &&
        (e === 1) | (this.AddGoodsList.pageIndex === '1')
      ) {
        // 如果页数和总页数一样则不让点击
        return
      }
      this.indexPage.pageIndex = String(Number(this.indexPage.pageIndex) + e)
      const data = {
        pageIndex: this.indexPage.pageIndex,
        pageSize: 10,
        skuName: this.form.id
      }
      const res = await getAddGoodsList(data)
      this.AddGoodsList = res
      this.threeLoad = false
    },
    delImgFn (index) {
      // 第二个框删除商品
      console.log(index)
      this.Aisle[index].sku = null
    },
    addGood (index) {
      // 第二个框添加商品需要第三个框确认
      this.checkIndex = index
    },
    threeClose () {
      // 第三个框关闭事件
      this.dialog_three = false
      this.form.id = ''
      this.indexPage.pageIndex = '1'
    },
    threeClick (check) {
      // 选择添加商品
      this.AddGoodsList.currentPageRecords.forEach((item) => {
        item.discount = false
      })
      // console.log(check)
      check.discount = true
      this.checkItemThird = check
    },
    threeConfirm () {
      // 第三个框确认把第二个框选中数据替换
      this.Aisle[this.checkIndex].sku = this.checkItemThird
      this.dialog_three = false
    },
    async getPolicy (data) {
      // 请求策略
      this.chooseItem = data
      const res = await getVmPolicy(data.innerCode)
      if (res === '') {
        const policy = await getPolicy(data.innerCode)
        this.Policy = policy
      }
      this.isPolicy = res
    },
    policyCancel () {
      // 关闭第四层清空
      this.adviceDia = false
      this.isPolicy = ''
      this.policyValue.value = ''
    },
    async addPolicy () {
      // 发布第四层策略
      const data = {
        innerCodeList: [this.chooseItem.innerCode],
        policyId: this.policyValue.value
      }
      const res = await addPolicy(data)
      this.$message.success(res ? '操作成功' : res)
      this.$parent.getDateS()
      this.policyCancel()
    },
    async delpolicy () {
      // 取消第四层策略
      const data = {
        innerCodeList: this.chooseItem.innerCode,
        policyId: this.isPolicy.policyId
      }
      const res = await delVmPolicy(data)
      this.$message.success(res ? '操作成功' : res)
      this.adviceDia = false
      this.$parent.getDateS()
    },
    async changeItemData (data) {
      // 修改弹窗
      // console.log(data)
      this.changeItem = data
      this.changeSelect.value = data.node.id
      const res = await getVmNode()
      this.changeOption = res.currentPageRecords
    },
    changeCancel () { // 修改弹窗取消
      this.changeDia = false
      this.changeItem = {}
      this.changeOption = []
      this.changeSelect.value = ''
    },
    async changeVmNode () { // 修改弹窗修改设备点位
      const data = {
        id: this.changeItem.id,
        nodeId: this.changeSelect.value
      }
      try {
        const res = await changeVmNode(data)
        console.log(res)
        this.$message.success('修改成功')
        this.changeDia = false
      } catch (e) {
        this.$message.error(e.response.data)
      }
    }
  },
  created () {}
}
</script>
