<template>
  <div v-loading="loading">
    <el-row :gutter="20">
      <el-col :span="24">
        <div class="top">
          <el-form
            :inline="true"
            :model="form"
            class="demo-form-inline"
            ref="search"
          >
            <el-form-item class="top_input_text" label="设备编号:" prop="id">
              <el-input
                v-model.number.trim="form.id"
