                class="top_input"
                size="medium"
                clearable
              >
              </el-input>
            </el-form-item>
            <el-button
              class="top_button"
              type="primary"
              icon="el-icon-search"
              size="medium"
              @click.native="getDateS(form.id)"
              >搜索</el-button
            >
          </el-form>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <div class="bottom_top">
          <el-button
            type="primary"
            class="bottom_button_add"
            size="medium"
            icon="el-icon-circle-plus-outline"
            @click="chooseFn"
            >新增</el-button
          >
          <el-button @click="doMore" class="bottom_button_do"
            >批量操作</el-button
          >
        </div>
        <div class="bottom_bottom">
          <div class="bottom_bottom_tittle">
            <span class="row_text">
              <el-checkbox
                :indeterminate="isIndeterminate"
                v-model="checkAll"
                @change="handleCheckAllChange"
              ></el-checkbox>
            </span>
            <span class="row_text">序号</span>
            <span class="row_text">设备编号</span>
            <span class="row_text">设备型号</span>
            <span class="row_text">详细地址</span>
            <span class="row_text">合作商</span>
            <span class="row_text">设备状态</span>
            <span class="row_text">操作</span>
          </div>
          <template v-if="cities[0]">
            <div
              class="bottom_bottom_body"
              v-for="(item, index) in cities"
              :key="index"
            >
              <span class="row_text">
                <el-checkbox-group
                  v-model="innerCodeList"
                  @change="handleCheckedCitiesChange"
                >
                  <el-checkbox :label="item.innerCode"> <br /></el-checkbox>
                </el-checkbox-group>
              </span>
              <span class="row_text">{{
                index + 1 + (dataS.pageIndex - 1) * 10
              }}</span>
              <span class="row_text">{{ item.innerCode }}</span>
              <span class="row_text">{{
                item.type ? item.type.name : "这TM没数据,这后端真坑人"
              }}</span>
              <span class="row_text">{{ item.node.addr.replace(/.*-/,'') }}</span>
              <span class="row_text">{{ item.node.ownerName }}</span>
              <span class="row_text">{{
                item.vmStatus === 0
                  ? "未投放"
                  : item.vmStatus === 1
                  ? "运营"
                  : item.vmStatus === 3
                  ? "撤机"
                  : ""
              }}</span>
              <span class="row_text right">
                <a href="javascript:;" @click="goodsAsideFn(item)">货道</a>
                <a href="javascript:;" @click="Policy(item)">策略</a>
                <a href="javascript:;" @click="changeDia(item)">修改</a>
              </span>
            </div>
          </template>
          <div v-else class="bottom_bottom_body">
            <div>
              <span class="none_text">暂无数据</span>
            </div>
          </div>
          <div class="bottom_bottom_bottom" v-if="cities[0]">
            <template v-if="dataS.totalPage">
              <span>
                <span class="bottom_text">共{{ dataS.totalCount }}条记录</span>
                <span class="bottom_text"
                  >第{{ dataS.pageIndex }}/{{ dataS.totalPage }}页</span
                >
              </span>
            </template>

            <span>
              <span class="button_all">
                <el-button
                  class="pre_button"
                  :disabled="dataS.pageIndex === '1' ? true : false"
                  :style="
                    dataS.pageIndex === '1'
                      ? 'backgroundColor:#EAEDF8;color:#D3D9E0'
                      : 'backgroundColor:#d6def6'
                  "
                  size="medium"
                  @click="dataS.pageIndex--, getDateS()"
                  >上一页</el-button
                >
              </span>
              <span class="button_all">
                <el-button
                  class="next_button"
                  style="background-color: #d6def6"
                  size="medium"
                  :disabled="dataS.pageIndex === dataS.totalPage ? true : false"
                  :style="
                    dataS.pageIndex === dataS.totalPage
                      ? 'backgroundColor:#EAEDF8;color:#D3D9E0'
                      : 'backgroundColor:#d6def6'
                  "
                  @click="dataS.pageIndex++, getDateS()"
                  >下一页</el-button
                >
              </span>
            </span>
          </div>
        </div>
      </el-col>
    </el-row>
    <!-- --弹出层新增-- -->
    <el-dialog
      title="收货地址"
      :visible.sync="showBox"
      width="630px"
      height="300px"
    >
      <el-form :model="goodsAdd" :rules="goodsRule" ref="addDevice">
        <el-form-item label="设备编号:" :label-width="formLabelWidth">
          <span>系统自动生成</span>
        </el-form-item>
