              :key="index"
              :value="item.typeId"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 选项二 -->
        <el-form-item
          label="选择策略"
          prop="nodeId"
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="goodsAdd.nodeId"
            class="chose_two"
            placeholder="请选择"
          >
            <el-option
              popper-class="list_bottom"
              :label="item.name"
              v-for="(item, index) in nodeID"
              :key="index"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button
          @click="(showBox = false), $refs.addDevice.resetFields()"
          class="left"
          size="medium"
          >取消</el-button
        >
        <el-button
          type="primary"
          @click="addDeviceFn"
          size="medium"
          class="right"
          >确定</el-button
        >
      </div>
    </el-dialog>
    <!-- --弹出层操作2-- -->
    <el-dialog
      class="dialog_do"
      title="批量策略管理"
      :visible.sync="showBoxDo"
      width="630px"
    >
      <el-form :model="boxChoose" ref="doMore">
        <!-- 选项一 -->
        <el-form-item
          label="选择型号:"
          prop="policyId"
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="boxChoose.policyId"
            class="chose_one"
            placeholder="请选择"
            popper-class="list_top"
          >
            <el-option
              :label="item.policyName"
              v-for="(item, index) in policyList"
              :key="index"
              :value="item.policyId"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button
          @click="(showBoxDo = false), $refs.doMore.resetFields()"
          class="left"
          size="medium"
          >取消</el-button
        >
        <el-button type="primary" @click="addDoMore" size="medium" class="right"
          >确定</el-button
        >
      </div>
    </el-dialog>
    <GoodAisle :out.sync="out" :inner.sync="inner" ref="GAisle"></GoodAisle>
  </div>
</template>

<script>
import GoodAisle from './components/goodAisle.vue'
import {
  getManage,
  getSearch,
  chooseType,
  chooseNode,
  addDevice,
  getPolicy,
  addPolicy

} from '@/api/vm'
export default {
  components: {
    GoodAisle
  },
  data () {
    return {
      form: {
        id: ''
      },
      checkAll: false,
      innerCodeList: [],
      cities: [],
      isIndeterminate: false,
      dataS: {
        pageIndex: '0',
        pageSize: '',
        totalCount: '',
        totalPage: ''
      },
      loading: false,
      // ------弹出层新增
      vmType: [],
      nodeID: [],
      showBox: false,
      goodsAdd: {
        createUserId: 1,
        nodeId: '',
        vmType: ''
      },
      goodsRule: {
        vmType: [{ required: true, message: '请选择' }],
        nodeId: [{ required: true, message: '请选择' }]
      },
      formLabelWidth: '110px',
      // 弹出层操作,
      showBoxDo: false,
      boxChoose: {
        policyId: ''
      },
      policyList: [],
      out: false,
      inner: false
    }
  },
  methods: {
    handleCheckAllChange (val) {
      if (val) {
        this.cities.forEach((item) => {
          if (!this.innerCodeList.includes(item.innerCode)) { // 如果选中的item不在列表中,则加入组里
            this.innerCodeList.push(item.innerCode)
          }
        })
      }
      this.innerCodeList = val ? this.innerCodeList : [] // 将选项加入已选择数组
      this.isIndeterminate = false // 将非全选状态设定为 -
    },
    handleCheckedCitiesChange (value) {
      const checkedCount = value.length // 每次改变传入数组长度
      this.checkAll = checkedCount === this.cities.length // 如果长度等于数组长度则全选
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length // 长度大于一且小于数组长度状态为-
    },
    async getDateS (id) {
      this.loading = true
      let res = []
      if (id) {
        const data = {
          pageIndex: 1,
          pageSize: 10,
          innerCode: id
        }
        res = await getSearch(data) // 搜索
      } else {
        res = await getManage(this.dataS) // 上一页下一页
      }
      this.cities = res.currentPageRecords
      this.dataS.pageIndex = res.pageIndex
      this.dataS.pageSize = res.pageSize
      this.dataS.totalPage = res.totalPage
      this.dataS.totalCount = res.totalCount
      this.innerCodeList = []
      this.isIndeterminate = false
      this.checkAll = false
      this.loading = false
    },
    async chooseFn () {
      // 请求新增机器数据
      this.showBox = true
      const type = await chooseType()
      this.vmType = type.currentPageRecords
      const node = await chooseNode()
      this.nodeID = node.currentPageRecords
    },
    async addDeviceFn () {
      // 发布新增机器
      await addDevice(this.goodsAdd)
      this.getDateS()
      this.showBox = false
      this.$refs.search.resetFields()
      this.$refs.addDevice.resetFields()
      this.$message.success('新增成功')
    },
    async doMore () {
      // 请求新增策略
      if (!this.innerCodeList.length) {
        return this.$message.warning('请勾选售货机')
      }
      this.showBoxDo = true
      const res = await getPolicy()
      this.getPolicy = res
    },
    async addDoMore () {
      // 发布新增策略
      const data = {
        innerCodeList: this.innerCodeList,
        policyId: this.boxChoose.policyId
      }
      await addPolicy(data)
      this.showBoxDo = false
      this.$refs.doMore.resetFields()
      await this.getDateS(0)
      this.$message.success('操作成功')
    },
    // 货道函数
    goodsAsideFn (data) {
      this.$refs.GAisle.sendItem(data)
      this.out = true
    },
    // 点击修改子组件策略弹出层显示
    Policy (data) {
      this.$refs.GAisle.getPolicy(data)
      this.$refs.GAisle.adviceDia = true
    },
    // 点击修改子组件策略弹出层
    changeDia (data) {
      this.$refs.GAisle.changeDia = true
      this.$refs.GAisle.changeItemData(data)
    }
  },
  async created () {
    await this.getDateS()
  }
}
</script>

