import request from '@/utils/request'
// 设备管理页面
export const getManage = data => request.get('/api/vm-service/vm/search', {
  params: data
})

// 搜索值
export const getSearch = data => request.get('/api/vm-service/vm/search', {
  params: data
})

// 选择型号
export const chooseType = () => request.get('/api/vm-service/vmType/search', {
  params: {
    pageIndex: 1,
    PageSize: 100000
  }
})

// 选择点位
export const chooseNode = () => request.get('/api/vm-service/node/search', {
  params: {
    pageIndex: 1,
    PageSize: 100000
  }
})

// 新增
export const addDevice = data => request.post('/api/vm-service/vm', data)

// 请求策略
export const getPolicy = () => request.get('/api/vm-service/policy')

// 添加策略
export const addPolicy = data => request.put('/api/vm-service/vm/applyPolicy', data)

// 以下货道接口
// 货道信息/行/列
export const getAisleRow = id => request.get(`/api/vm-service/vmType/${id}`)

// 货道详情图片
export const getAisle = id => request.get(`/api/vm-service/channel/channelList/${id}`)

// 智能排序图片详情
export const getSort = id => request.get(`/api/vm-service/sku/businessTop10/${id}`)

// 智能排序商圈
export const getBusiness = id => request.get(`/api/vm-service/businessType/name/${id}`)

// 添加商品 (搜索和切换页面)
export const getAddGoodsList = data => request.get('/api/vm-service/sku/search', {
  params: data
})

// 发布新增商品(点击确认采取意见&&手动添加删除)
export const putVm = data => request.put('/api/vm-service/channel/channelConfig', data)

// 查询详细有没有策略
export const getVmPolicy = id => request.get(`/api/vm-service/policy/vmPolicy/${id}`)

// 取消策略
export const delVmPolicy = data => request.put(`/api/vm-service/vm/cancelPolicy/${data.innerCodeList}/${data.policyId}`)

// 点位搜索
export const getVmNode = () => request.get('/api/vm-service/node/search', {
  params: {
    pageIndex: 1,
    pageSize: 10000
  }
})

// 修改点位
export const changeVmNode = data => request.put(`/api/vm-service/vm/${data.id}/${data.nodeId}`)

// 设备详情的供应次数
export const getDetailSupply = data => request.get(`/api/task-service/task/supplyCount/${data.innerCode}/${data.start}/${data.end}`)

// 设备详情的维修次数
export const getDetailRepair = data => request.get(`/api/task-service/task/repairCount/${data.innerCode}/${data.start}/${data.end}`)

// 设备详情的商品详情
export const getDetailSku = data => request.get(`/api/order-service/report/skuCollect/${data.innerCode}/${data.start}/${data.end}`)

// 设备详情的销售额
export const getDetailOrderAmount = data => request.get('/api/order-service/report/orderAmount', {
  params: data
})

// 设备详情的销售数量
export const getDetailOrderCount = data => request.get('/api/order-service/report/orderCount', {
  params: data
})

// 获取设备类型列表
export const getTypeList = data => request.get('/api/vm-service/vmType/search', { params: data })

// 搜索设备类型
export const searchType = data => request.get('/api/vm-service/vmType/search', { params: data })

// 图片接口
export const uploadImg = data => request.post('/api/vm-service/sku/fileUpload/')

// 新增机器类型
